




























import { Component, Prop, Vue, Emit } from 'vue-property-decorator';

@Component({})
export default class FooterComp extends Vue {
  @Prop() windowSize: any;
  @Prop() currentUser: any;
  pageNum:number = 4;
  get pageColor() {
    switch (this.pageNum) {
      case 0 : return 'blue-grey'
      case 1 : return 'teal'
      case 2 : return 'brown'
      default : return '#666666'
    }
  }

}
